import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from '@/pages/index'
import DetailPage from '@/pages/detail'
import DetailAnaPage from '@/pages/detail/analysis'
import DetailCouPage from '@/pages/detail/count'
import DetailForPage from '@/pages/detail/forecast'
import DetailPubPage from '@/pages/detail/publish'
import orderList from '@/pages/orderList'

Vue.use(Router)

export default new Router({
    mode: 'history', // hash(#)
    routes: [{				// -- router !== routes
            path: '/web_list/vuejs-shopping',
            component: IndexPage   // -- componentes !== component
        	},{
            path: '/web_list/vuejs-shopping/orderList',
            component: orderList
        	},{
			path: '/web_list/vuejs-shopping/detail',
			component: DetailPage,
			redirect: '/web_list/vuejs-shopping/detail/analysis',   // detail/detail/analysis
			children: [
				{
					path: 'analysis',
					component: DetailAnaPage
				},
				{
					path: 'count',
					component: DetailCouPage
				},
				{
					path: 'forecast',
					component: DetailForPage
				},
				{
					path: 'publish',
					component: DetailPubPage
				}
			]
		}]

})



